

.conacts__cards-list{
  @media screen and (min-width: 768px)  {
    display: flex;
    gap: 16px;
    margin-bottom: 60px;
  }
  @media screen and (min-width: 1200px)  {  
    gap: 32px;
    margin-bottom: 64px;
  }
}
.conacts__cards-item {
  padding: 40px 32px;
  max-width: 450px;
  width: 100%;
  height: 412px;
  border-radius: 24px;
  background-color: var(--color-white-main);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  &:not(:last-child) {
    margin-bottom:20px ;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding: 40px 24px;
    width: 224px;
    height: 528px;
    &:not(:last-child) {
      margin-bottom:0 ;
    }
  }
  @media screen and (min-width: 1200px)  {
    padding: 40px 40px;
    width: 368px;
    height: 538px;
    &:not(:last-child) {
      margin-bottom:0 ;
    }
  }
}
.office {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86; 
  letter-spacing: 0.04em;

  display: flex;
  width: 80px;
  height: 16px;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;  

  color: var(--color-black-main); 
  background-color: var(--color-yellow-main);
  border-radius: 4px;
  @media screen and (min-width: 768px)  {
    width: 73px;
    height: 28px;
    font-size: 16px;
    margin-bottom: 32px;
  }
  
}

.office--big{
    width: 130px; 
    @media screen and (min-width: 768px)  {
      width: 138px; 
    }
}
.office__adress{
    font-style: normal; 

    padding-bottom: 24px;
    border-bottom: 1px solid #E1E1E1;
    margin-bottom: 24px;
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      padding-bottom: 26px;
      margin-bottom: 32px;
    }
    @media screen and (min-width: 1200px)  {
      padding-bottom: 32px;     
    }
}
.office__town{
    font-weight: 500;
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.04em;
    
    margin-bottom: 8px;  

    color: var(--color-black-main); 
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      font-size: 14px;
      margin-bottom: 12px;  
    }
    @media screen and (min-width: 1200px)  {
      font-size: 16px;
      margin-bottom: 8px;  
    }

}

.office__street{
    display: block;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.83;
    letter-spacing: 0.04em;
    width: 190px;
   

    color: var( --color-brown-main); 
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      width: 173px;
      font-size: 14px;
    }
    @media screen and (min-width: 1200px) {
      width: 262px;
      font-size: 16px;
    }

}
.office__street--two{
    width: 212px; 
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      width: 158px;
    }
    @media screen and (min-width: 1200px) {
      width: 288px;
    }
}
.office__street--three{
    width: 185px; 
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      width: 159px;
    }
    @media screen and (min-width: 1200px) {
      width: 261px;
    }
}

.days__item{
  &:not(:last-child) {
    margin-bottom: 8px;
  }
  @media screen and (min-width: 768px)  {
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }
  
}

.days__week{
font-weight: 500;
font-size: 12px;
line-height: 1.86;


color: var(--color-black-main);
@media screen and (min-width: 768px) and (max-width: 1199px) {
  font-size: 14px;
}
@media screen and (min-width: 1200px)  {
  font-size: 16px;
}
}

.days__time{
font-weight: 500;
font-size: 12px;
line-height: 1.86;

color: var(--color-grey-main);
@media screen and (min-width: 768px) and (max-width: 1199px) {
  font-size: 14px;
}
@media screen and (min-width: 1200px)  {
  font-size: 16px;
}
}
.conacts__list-button {
  text-align: center;
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    gap: 16px;
  }
}
.connection{
  
  
  margin-top: 24px;
  padding-top: 24px;
  border-top:1px solid #E1E1E1; 
  @media screen and (min-width: 768px)  {
    margin-top: 32px;
    padding-top: 32px;
  }

}

.connection-link{
text-decoration: none; 
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;

  color: var(--color-black-main);
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1200px)  {
    font-size: 16px;
  }
}

.conacts__button {
  margin-top: 32px;
    margin-bottom: 16px; 
  @media screen and (min-width: 768px) {
    margin-bottom: 0;
    margin-top: 0;
  }
}